import React, { Component, PropTypes } from 'react';
import KlassName from 'teapan/klassName';

class Grid extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			guttered,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('grid',{
			['grid--guttered']:guttered,
		},className);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

Grid.propTypes = {
	guttered: PropTypes.bool,

	className: PropTypes.string,
	children: PropTypes.node,
};

Grid.defaultProps = {
	guttered: false,
};

export default Grid;